<template>
  <div class="d-flex m-4">
    <button
      class="btn btn-outline-danger mx-4 w-100"
      :disabled="step == 1"
      @click="step--"
    >
      Previous
    </button>
    <div class="container text-center">Time Left: {{ timeLeft }}</div>
    <button class="btn btn-outline-success mx-4 w-100" @click="step++">
      Next
    </button>
  </div>
</template>

<script>
export default {
  props: ["appStep"],
  mounted() {
    this.getTime();
  },
  data() {
    return {
      timeLeft: "",
    };
  },
  methods: {
    getTime() {
      const now = new Date();
      let left = new Date();
      left.setHours(17, 0, 0);

      if (now > left) {
        left.setDate(left.getDate() + 1);
      }

      const remain = (left - now) / 1000;
      const hh = parseInt((remain / 60 / 60) % 60);
      const mm = parseInt((remain / 60) % 60);
      const ss = parseInt(remain % 60);

      this.timeLeft = `${hh}:${mm}:${ss}`;
      setInterval(this.getTime, 1000);
    },
  },
  computed: {
    step: {
      get() {
        return this.appStep;
      },
      set(val) {
        return this.$emit("change-step", val);
      },
    },
  },
};
</script>

<style></style>
